import * as actionType from './actionType'
import {getData} from 'util'
import {
  API_OVERDELIVER_LIST_PAGE,
  API_OVERDELIVER_ID_DELETE,
  API_OVERDELIVER_EXPORT
} from 'api'
//同步
export const update = (object) => ({
  type: actionType.GOODSOVERDELIVER_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) == '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  let {size, page} = reqData.searchParams
  dispatch({
    type: actionType.GOODSOVERDELIVER_UPDATE,
    data: {tableLoading: true}
  })
  getData({api: API_OVERDELIVER_LIST_PAGE, ...reqData})
    .then((data) => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.GOODSOVERDELIVER_UPDATE,
        data: {
          size,
          page,
          total: data.result.total,
          tableLoading: false,
          searchResult: data.result.list || []
        }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.GOODSOVERDELIVER_FILE
      })
    })
}

// 删除
export const handleDel = (reqData, callBack) => (dispatch) => {
  getData({api: API_OVERDELIVER_ID_DELETE, ...reqData})
    .then((data) => {
      callBack && callBack()
    })
    .catch((err) => {
      dispatch({
        type: actionType.GOODSOVERDELIVER_FILE
      })
    })
}
// 导出
export const handleExport = (reqData) => (dispatch) => {
  dispatch({
    type: actionType.GOODSOVERDELIVER_UPDATE,
    data: {buttonLoading: true}
  })
  let api = API_OVERDELIVER_EXPORT
  getData({api, ...reqData})
    .then((data) => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.GOODSOVERDELIVER_UPDATE,
        data: {
          buttonLoading: false
        }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.GOODSOVERDELIVER_FILE
      })
    })
}



