import React, {PureComponent} from 'react'
import {Form, Button, Row, Col, message} from 'antd'
import style from './style/Form.scss'
import {CsxSelect, CsxMultipleSelect} from 'Form'
import {connect} from 'react-redux'
import {proTypeCatorageList} from 'Mock'
import {getData} from 'util'
import {
  API_PRODUCTS_KEYVALUES,
  API_PRODUCT_CATEGORYSONS,
  API_MERCHANTPRIORITYCHOICE_KEYVALUES,
  API_USER_WAREHOUSELIST
} from 'api'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.goodsOverDeliver, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
    this.state = {
      searchProList: [] //商品列表
    }
  }

  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }

  //查询
  query = () => {
    const {handleGetList, searchParams = {}, size} = this.props
    const {categoryCodes = '', productType = ''} = searchParams
    console.log('searchParams is', searchParams)
    if (categoryCodes && !(productType && productType.value)) {
      return message.error('请选择商品类型')
    }
    handleGetList({searchParams: {...searchParams, page: 1, size}})
  }

  // 重置
  reset = () => {
    const {update, handleGetList, size} = this.props
    update({searchParams: {}})
    handleGetList({searchParams: {page: 1, size}})
  }
  getProductOfTypes = (type) => {
    let searchdata = {
      searchParams: {
        type
      }
    }
    getData({
      api: API_PRODUCTS_KEYVALUES, ...searchdata}).then(data => {
        this.setState({
          searchProList: data.result || []
        })
      })
      // 相应的逻辑判断
      .catch(err => {

      })
  }

  render() {
    const {
      searchParams: {
        productType = {},
        categoryCodes = [],
        shopType
      },
      modalTuttonLoading
    } = this.props
    const {searchProList = []} = this.state
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={6}>
            <CsxSelect
              dateApi={API_MERCHANTPRIORITYCHOICE_KEYVALUES}
              label='门店类型'
              selectItem={{
                data: shopType,
                key: 'merchantType',
                valueKey: 'merchantType'
              }}
              onChangeSelect={value => {
                this.onInputChange({shopType: value})
              }}
            />
          </Col>
          <Col span={6}>
              <CsxSelect
                data={proTypeCatorageList}
                label='类型选择'
                selectItem={{data: productType, key: 'key', valueKey: 'value'}}
                onChangeSelect={value => {
                  console.log('value is', value)
                  this.onInputChange({productType: value || ''})
                  // 获取不同类别的数据
                  this.getProductOfTypes(value.value || '')
                }}
              />
          </Col>
          <Col span={6}>
               <CsxMultipleSelect
                data={searchProList}
                require={false}
                label='查询值'
                placeholder='请输入'
                selectItem={{data: categoryCodes, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  console.log('value is', value)
                  this.onInputChange({categoryCodes: value})
                }}
                style={{width: 223}}
              />
          </Col>
          </Row>
        <Row>
        <Col span={24}>
            <Row type='flex' justify='end'>
              <Button
                type='primary'
                style={{marginRight: 20}}
                loading={modalTuttonLoading}
                onClick={this.query}
              >
                查询
              </Button>
              <Button type='primary' onClick={this.reset}>
                重置
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}
export default Form.create()(App)



