import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Modal
} from 'antd'
import {Import} from 'HOC'
import style from './style/Tool.scss'
import {API_WMS_OVERDELIVER_IMPORT,
  API_OVERDELIVER_EXPORT_ERROR
} from 'api'
import {connect} from 'react-redux'
import {getData} from 'util'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const confirm = Modal.confirm
@connect(
  state => ({...state.goodsOverDeliver}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  /* 导入成功 */
  handleImportSuccess = res => {
    const {update, handleGetList, searchParams, page, size} = this.props
    update({importBtnLoading: false})
    res && res.message === 'ok' && message.success('导入成功')
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  /* 报错导出 */
  handleExprotError = (data) => {
    console.log('data is', data)
    const {downloadUrl = ''} = data
    let urlPath = {
      // url: `${downloadUrl}`,
      url: 'overshipping/download/err',
      config: {
        method: 'get',
        host: 'CSXWMS'
      }
    }
    getData({
      api: urlPath}).then(() => {
        message('导出报错成功')
      })
  }
    //下载错误信息
  DownErrorInfo = () => {

  }
  //导出文件
  export = () => {
    const {handleExport, searchParams, page, size} = this.props
    handleExport({searchParams: {...searchParams, page, size}}, () => {
      message.success('导出成功')
    })
  }
  handleDelete = () => {
    const {selectedRowKeys = [], handleDel, handleGetList, searchParams, userLoation, page, size} = this.props
    let selectedRowKeys_length = selectedRowKeys && Array.isArray(selectedRowKeys) && selectedRowKeys.length ? selectedRowKeys.length : ''
    if (!selectedRowKeys_length) {
      message.error('请先选择需要批量删除的商品超发比例配置')
      return
    }
    console.log('selectedRowKeys is', selectedRowKeys)
    confirm({
      title: '温馨提示',
      content: `确认将这${selectedRowKeys_length}条商品超发比例配置删除？`,
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleDel({
          searchParams: {ids: selectedRowKeys}
        }, () => {
          message.success('批量删除成功')
          handleGetList({searchParams: {...searchParams, page, size}, userLoation})
        })
      }
    })
  }

  render() {
    const {importBtnLoading = false, buttonLoading = false} = this.props
    return (
      <div className={style['table-tool']}>
        <Button type="primary" className={style.btn} onClick={this.export} loading={buttonLoading}>导出</Button>
        <Import
          action={{api: API_WMS_OVERDELIVER_IMPORT}}
          onOk={this.handleImportSuccess}
          onExprotError={this.handleExprotError}
          urlToGetErrorList = {true}
          beforeImport={() => {
            const {update} = this.props
            update({tableLoading: true, importBtnLoading: true})
          }}
          onResponse={() => {
            const {update} = this.props
            update({tableLoading: false, importBtnLoading: false})
          }}
        >
          <Button loading={importBtnLoading} type="primary" className={style.btn}>导入</Button>
        </Import>
        <Button type="primary" className={style.btn} onClick={this.handleDelete}>批量删除</Button>
      </div>
    )
  }
}



