import React, {PureComponent} from 'react'
import {Table, Modal, message, Radio, Select} from 'antd'
const confirm = Modal.confirm
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.goodsOverDeliver, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {}
    this.columns = [
      {
        title: '门店类型',
        width: 100,
        dataIndex: 'shopType',
        key: 'shopType'
      },
      {
        title: '商品类型',
        width: 100,
        dataIndex: 'productType',
        key: 'productType'
      },
      {
        title: '类型名称',
        width: 120,
        dataIndex: 'categoryName',
        key: 'categoryName',
        render: (text, record) => `${record.categoryCode} - ${record.categoryName}`
      },
      {
        title: '超发比例',
        width: 100,
        dataIndex: 'overShippingProportion',
        key: 'overShippingProportion'
      },
      {
        title: '更新人',
        width: 120,
        dataIndex: 'updateBy',
        key: 'updateBy'
      },
      {
        title: '更新时间',
        width: 180,
        dataIndex: 'updateTime',
        key: 'updateTime'
      }
    ]
  }

  opera(dataRecord, type) {
    const {update, handleGetList, handleDel, size, searchParams} = this.props
    console.log(searchParams)
    if (type == 1) {
      console.log(dataRecord)
      update({modalType: type, modalData: dataRecord, isShowModal: true})
    }
    if (type == 2) {
      confirm({
        title: '温馨提示',
        content: '是否删除该条信息？',
        okText: '确认',
        width: 520,
        cancelText: '取消',
        onOk() {
          handleDel({urlParams: [dataRecord.id]}, () => {
            handleGetList({
              searchParams: {
                warehouseCode: searchParams.warehouseCode,
                page: 1,
                size
              }
            })
            message.success('删除成功')
          })
        }
      })
    }
  }

  componentDidMount() {
    const {handleGetList, size} = this.props
    handleGetList({searchParams: {page: 1, size}})
  }

  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, userLoation, size} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size}})
    }
  }

  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...searchParams
        }
      })
    }
  }

  render() {
    const {total, page, size, tableLoading, searchResult, selectedRowKeys} = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeys, selectedRows})
      }
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



